import React from 'react';

const accord = (props) => {
    const nbspChar = '\u00A0';

    let accords = [],
        currentPosition = 0;

    for (let i = 0; i < props.accords.length; i += 1) {

        let accord = props.accords[i];
        let accordPosition = accord.position;
        let countNbsp = accordPosition - currentPosition;

        let nbsps = (
            <span key={'whitespace' + i}>
                {Array(countNbsp).fill(nbspChar).join('')}
            </span>
        );
        let accordChar = (<span key={'accord' + i}>{accord.accord}</span>);

        accords.push(nbsps);
        accords.push(accordChar);

        currentPosition = accordPosition + (accord.accord.length);
    }

    return (
        <p>{accords}</p>
    );
};

export default accord;



