import {
    ADD_NEW_TEXT,
    UPDATE_TEXT,
    UPDATE_ACCORD
} from '../actions/actionTypes';

const initState = {
    accords: [{
        accords: [{
            accord: 'bm',
            position: 10
        }],
        text: 'you can holler, you can wail'
    },{
        accords: [{
            accord: 'D',
            position: 0
        }],
        text: 'You can swing, you can flail '
    },{
        accords: [{
            accord: 'G',
            position: 0
        }, {
            accord: 'D',
            position: 17
        }],
        text: 'You can fuck like a broken sail'
    },{
        accords: [{
            accord: 'B',
            position: 12
        }],
        text: 'But I\'ll never give you up'
    },{
        accords: [{
            accord: 'D',
            position: 0
        }, {
            accord: 'G',
            position: 20
        }, {
            accord: 'D',
            position: 32
        }],
        text: 'If I ever give you up my heart will surely fail.'
    }]
};

// const initState = {
//     accords: []
// };


const addNewText = (state, action) => {

    const accords = Object.assign([], state.accords);
    accords.push({
        text: action.text,
        accords: []
    });

    return Object.assign({}, state, {
        accords: accords
    })
};

const updateAccord = (state, action) => {

    const accords = Object.assign([], state.accords);
    const accordId = accords[action.lineId].accords.findIndex(el => el.position === action.charId);

    // if empty accord - remove he, else update
    if (action.accord === '') {
        accords[action.lineId].accords.splice(accordId, 1);
    } else {

        // if accord is set update else add new
        if (accordId !== -1) {
            accords[action.lineId].accords[accordId].accord = action.accord
        } else {
            accords[action.lineId].accords.push({
                accord: action.accord,
                position: action.charId
            });
        }
    }

    // sort accords by position
    accords[action.lineId].accords = [...accords[action.lineId].accords].sort((a, b) => a.position - b.position);

    return Object.assign({}, state, {
        accords: accords
    });
};

const updateText = (state, action) => {
    const accords = Object.assign([], state.accords);
    accords[action.lineId].text = action.text;
    return Object.assign({}, state, {
        accords: accords
    });
};

////

const reducer = (state = initState, action) => {
    switch (action.type) {
        case ADD_NEW_TEXT:      return addNewText(state, action);
        case UPDATE_TEXT:       return updateText(state, action);
        case UPDATE_ACCORD:     return updateAccord(state, action);
        default:                return state;
    }
};

export default reducer;



