import React, { Component } from 'react';
import { connect } from 'react-redux';

import classes from './Char.css';
import Popover from '../../../UI/Popover/Popover';
import { updateAccord } from '../../../../store/actions/index';

class Char extends Component {
    state = {
        showPopover: false,
        accord: this.props.accord ? this.props.accord : '',
        charId: this.props.charId,
        lineId: this.props.lineId
    }

    onShowPopoverHandler = () => {
        this.setState({ showPopover: true });
    }

    onHidePopoverHandler = () => {
        this.setState({ showPopover: false });
    }

    onChangeAccordHandler = (e) => {
        this.setState({ accord: e.target.value });
    }

    onSaveAccordHandler = (e) => {
        e.preventDefault();
        this.props.onAddNewAccord(this.state.accord, this.state.charId, this.state.lineId);
        this.setState({ showPopover: false });
    }

    render () {
        return (
            <span className={classes.Char} title='Add Accord'>
                <Popover
                    accord={this.state.accord}
                    show={this.state.showPopover}
                    onHidePopover={this.onHidePopoverHandler}
                    onChangeAccord={this.onChangeAccordHandler}
                    onSaveAccord={this.onSaveAccordHandler}
                />
                <span onClick={this.onShowPopoverHandler}>
                    {this.props.char}
                </span>
            </span>
        );
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        onAddNewAccord: (accord, charId, lineId) => dispatch(updateAccord(accord, charId, lineId))
    }
};

export default connect(null, mapDispatchToProps)(Char);



