import React, { Component } from 'react';
import { connect } from 'react-redux';
import FileSaver from 'file-saver';

class SaveFile extends Component {

    onSaveFileHandler = () => {
        const tablature = JSON.stringify(this.props.accords, null, 2);
        const file = new File([tablature], "accords.json", {type: "application/json;charset=utf-8"});
        FileSaver.saveAs(file);
    }

    render() {
        return (
            <button type='button' onClick={this.onSaveFileHandler}>Save</button>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        accords: state.accord
    };
}

export default connect(mapStateToProps)(SaveFile);



