import React, { Fragment } from 'react';

import Accord from './Accord/Accord';
import Text from './Text/Text';

const lyrics = (props) => {
    return (
        <Fragment>
            {
                props.accords.map((accord, key) => {
                    return !accord.text
                        ? null
                        : (
                            <pre key={key}>
                                {accord.accords ? <Accord id={key} accords={accord.accords}/> : null}
                                {accord.text ? <Text id={key} text={accord.text} accords={accord.accords}/> : null}
                            </pre>
                        )
                })
            }
        </Fragment>
    );
};

export default lyrics;



