import React, { Component } from 'react';
import { connect } from 'react-redux';

import classes from './Accords.css';
import Lyrics from '../../components/Lyrics/Lyrics';
import Input from '../../components/Input/Input';
import SaveFile from '../../components/UI/SaveFile/SaveFile';
import LoadFile from '../../components/UI/LoadFile/LoadFile';

class Accords extends Component {
    render() {
        return (
            <div className={classes.Accords}>
                <Lyrics accords={this.props.accords}/>
                <hr/>
                <Input/>
                <SaveFile/>
                <LoadFile/>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        accords: state.accord.accords
    };
};

export default connect(mapStateToProps)(Accords);



