import React, { Component } from 'react';
import { connect } from 'react-redux';

import Char from './Char/Char';
import { updateText } from '../../../store/actions/index';

class Text extends Component {

    state = {
        text: this.props.text,
        isEdit: false
    }

    onOpenEditHandler = () => {
        this.setState({ isEdit: true });
    }

    onChangeTextHandler = (e) => {
        this.setState({ text: e.target.value });
    }

    onUpdateTextHandler = () => {
        this.props.onUpdateText(this.props.id, this.state.text);
        this.setState({ isEdit: false });
    }

    render () {
        return (
            <div>
                {this.state.isEdit
                    ? <input type='text' onChange={this.onChangeTextHandler} value={this.state.text}/>
                    : this.state.text.split('').map((char, key) => {

                        const charAccords = this.props.accords.find(el => el.position === key);
                        const accord = (charAccords && charAccords.accord) ? charAccords.accord : null;

                        return <Char
                            key={key}
                            charId={key}
                            lineId={this.props.id}
                            char={char}
                            accord={accord}
                        />
                    })
                }
                {this.state.isEdit
                    ? <button type='button' onClick={this.onUpdateTextHandler}>save</button>
                    : <button type='button' onClick={this.onOpenEditHandler}>edit</button>
                }
            </div>
        );
    }
}

const mapDispatchTpProps = (dispatch) => {
    return {
        onUpdateText: (lineId, text) => dispatch(updateText(lineId, text))
    }
};

export default connect(null, mapDispatchTpProps)(Text);



