import {
    ADD_NEW_TEXT,
    UPDATE_TEXT,
    UPDATE_ACCORD
} from './actionTypes';

const addNewText = (text) => {
    return {
        type: ADD_NEW_TEXT,
        text: text
    }
};

export const updateText = (lineId, text) => {
    return dispatch => {
        dispatch({
            type: UPDATE_TEXT,
            lineId: lineId,
            text: text
        })
    };
};

export const updateAccord = (accord, charId, lineId) => {
    return {
        type: UPDATE_ACCORD,
        accord: accord,
        charId: charId,
        lineId: lineId
    }
};

export const newText = (text) => {
    return dispatch => {
        dispatch(addNewText(text))
    }
};



