import React, { Fragment } from 'react';

import classes from './Popover.css';
import Overlay from '../Overlay/Overlay';

const popover = (props) => {
    return !props.show
        ? null
        : (
            <Fragment>
                <Overlay clicked={props.onHidePopover}/>
                <span className={[classes.Popover, classes.ArrowBottom].join(' ')}>
                    <form onSubmit={props.onSaveAccord}>
                        <input type='text' value={props.accord} onChange={props.onChangeAccord}/>
                        <input type='submit' style={{ display: 'none' }}/>
                    </form>
                </span>
            </Fragment>
        )
};

export default popover;



